<template>
  <div class="Usercontrol">
    <h3>用户个人信息：</h3>
    <el-form
      :rules="userRules"
      id="userDataForm"
      ref="userdataForm"
      label-width="80px"
      :model="loginUserData"
      class="elForm"
    >
      <el-form-item label="用户名：" prop="username">
        <el-input v-model="loginUserData.username"></el-input>
      </el-form-item>
      <el-form-item label="手机号：">
        <el-input v-model="loginUserData.phone" disabled></el-input>
      </el-form-item>
      <el-form-item label="密码：" prop="password">
        <el-input
          :type="pwdview ? 'text': 'password'"
          placeholder="请输入密码"
          v-model="loginUserData.password"
          prefix-icon="el-icon-cpu"
        >
          <i
            @click="pwdview = !pwdview"
            class="pwdview loginpassword"
            slot="suffix"
            :class="pwdview ? 'el-icon-unlock': 'el-icon-lock'"
          ></i>
        </el-input>
      </el-form-item>
      <el-form-item label="头像：">
        <!-- 头像上传区域 -->
        <el-upload
          class="avatar-uploader"
          :action="$http.defaults.baseURL+'/uplod'"
          name="img"
          :show-file-list="false"
          :on-success="headsuccess"
          :on-progress="headupload"
        >
          <img
            v-if="loginUserData.head_img"
            :src="loginUserData.head_img |imgFilter"
            class="avatar cursorp"
          />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <hr />
        <el-button @click="setUserFn" type="primary" style="float:right;">保存更改</el-button>
      </el-form-item>
    </el-form>
    <h3>用户导航控制面板：</h3>
    <div id="conbox" class="control_box">
      <div v-for="(item,i) in navinit" :key="i">
        <div @click="navclick(i)" :class="{'noselect':navs[i] == 0,'cursorp':true}">{{item}}</div>
      </div>
    </div>
    <div class="outBtn">
      <div class="cursorp" @click="exit">退出登录</div>
    </div>
    <!-- 开启导航弹出框 -->
    <el-dialog
      :class="{'upnone':filelist.length >= 1}"
      :title="`修改“${navinit[index]}”信息`"
      :visible.sync="opennavflage"
      id="loginDlog"
      :before-close="handleClose"
    >
      <el-form :model="navitemdata" :rules="rules" class="elForm" ref="updialog">
        <el-form-item prop="title">
          <el-input v-model="navitemdata.title" placeholder="主标题" prefix-icon="el-icon-sunny"></el-input>
        </el-form-item>
        <el-form-item prop="ctitle1">
          <el-input v-model="navitemdata.ctitle1" placeholder="子标题1" prefix-icon="el-icon-sunrise"></el-input>
        </el-form-item>
        <el-form-item prop="ctitle2">
          <el-input
            v-model="navitemdata.ctitle2"
            placeholder="子标题2"
            prefix-icon="el-icon-sunrise-1"
          ></el-input>
        </el-form-item>
      </el-form>
      <!-- 文件上传区域 -->
      <el-upload
        ref="upload"
        :action="$http.defaults.baseURL+'/uplod'"
        name="img"
        list-type="picture-card"
        :limit="1"
        :on-success="upsucceed"
        :on-exceed="upexceed"
        :on-progress="upprogress"
        :on-error="uperror"
        :on-remove="imgremove"
        :file-list="filelist"
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <i class="imgtitle">只能上传一张图片哟~</i>
      <div class="outBtn">
        <div
        class="cursorp"
          @click="isopennav(navs[index])"
          :style="navs[index] == '0' ? 'background-color:skyblue':'background-color:red'"
        >{{navs[index] == '0' ? '开启此项访问':'关闭此项访问'}}</div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="baocun">保存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState({
      showData: "allnavs", //导航数据
    }),
    ...mapState(["loginUserData","isLogin"])
  },
  data() {
    return {
      pwdview: false,
      navs: [],
      // 打开导航菜单
      opennavflage: false,
      //   初始化数据
      navinit: ["简介", "博客", "剪影", "伴侣", "旅行", "留言"],
      index: 0,
      //   导航数据
      navitemdata: {
        title: "",
        ctitle1: "",
        ctitle2: "",
        img_url: ""
      },
      filelist: [],
      //   校验规则
      rules: {
        title: [
          { required: true, message: "请输入主标题", trigger: "change" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "change" }
        ],
        ctitle1: [
          { required: true, message: "请输入子标题", trigger: "change" },
          { min: 3, max: 9, message: "长度在 3 到 9 个字符", trigger: "change" }
        ],
        ctitle2: [
          { required: true, message: "请输入子标题", trigger: "change" },
          { min: 3, max: 9, message: "长度在 3 到 9 个字符", trigger: "change" }
        ]
      },
      userRules: {
        username: [
          { required: true, message: "请输入活动名称", trigger: "change" },
          {
            min: 3,
            max: 16,
            message: "长度在 3 到 16 个字符",
            trigger: "change"
          }
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "change" },
          {
            min: 6,
            max: 12,
            message: "长度在 6 到 12 个字符",
            trigger: "change"
          }
        ]
      }
      //   回显数据
      // showData: []
    };
  },
  methods: {
    ...mapMutations([
      "setLoadingFlage" //加载函数
    ]),
    ...mapActions({
      getnavs: "getAllnavs" //获取导航状态
    }),
    ...mapActions(["getLoginUserData"]),
    handleClose() {
      this.navitemdata.img_url = null;
      this.filelist = [];
      this.opennavflage = false;
    },
    isopennav(val) {
      let value = val == 1 ? 0 : 1;
      this.navs[this.index] = value;
      this.navs = this.navs.join(",").split(",");
      //   保存上传数据
      this.baocun();
    },
    baocun() {
      this.$refs["updialog"].validate(async valid => {
        if (valid) {
          this.setLoadingFlage(true)
          let obj = {};
          obj.navs = this.navs.join(",");
          obj[`n${this.index + 1}_img`] = this.navitemdata.img_url;
          obj[
            `n${this.index + 1}`
          ] = `${this.navitemdata.title},${this.navitemdata.ctitle1},${this.navitemdata.ctitle2}`;
          let { data: res } = await this.$http.post("/navs", obj);
          this.setLoadingFlage(false)
          if (res.ok == 0) {
            this.getnavs();
            return this.$notify.error({
              title: "更改失败~",
              customClass: "alter"
            });
          }
          this.$notify.success({
            title: "更改成功~",
            customClass: "alter"
          });
          this.opennavflage = false;
          //   清空表单
          this.$refs["updialog"].resetFields();
          this.navitemdata.img_url = null;
        }
        // 不管成功与否都获取数据
        this.getnavs();
      });
    },
    // 退出登录
    exit() {
      sessionStorage.clear();
      // 刷新页面
      location.href = location.href.replace(/\/usercontrol/, "");
    },
    // 点击盒子
    navclick(index) {
      this.filelist = [];
      // 判断是否有图片
      if (
        this.showData[`n${index + 1}_img`] != null &&
        this.showData[`n${index + 1}_img`] != ""
      ) {
        this.filelist.unshift({
          url: this.showData[`n${index + 1}_img`].replace(
            /^(http:\/\/\d+\.\d+\.\d+\.\d+:\d{4}\/)|\//,
            this.$http.defaults.baseURL + "/"
          )
        });
        this.navitemdata.img_url = this.showData[`n${index + 1}_img`];
      }
      this.index = index;
      let arr = this.showData[`n${index + 1}`].split(",");
      this.navitemdata.title = arr[0];
      this.navitemdata.ctitle1 = arr[1];
      this.navitemdata.ctitle2 = arr[2];
      this.opennavflage = true;
    },
    // 文件上传钩子组
    upsucceed(res, file, filelist) {
      this.filelist = filelist;
      this.setLoadingFlage(false)
      // 判断是否上传成功
      if (res.ok == 0) {
        setTimeout(() => {
          this.filelist.pop();
        }, 1000);
        this.$refs.upload.clearFiles();
        return this.$notify.error({
          title: "图片不能超过4M哟",
          customClass: "alter"
        });
      }

      // 成功提示
      this.$notify.success({
        title: "上传成功~",
        customClass: "alter"
      });
      //   保存图片路径
      this.navitemdata.img_url = res.src;
    },
    upexceed() {
      this.$notify.error({
        title: "只能上传一张图片",
        customClass: "alter"
      });
    },
    upprogress() {
      this.setLoadingFlage(true)
    },
    uperror() {
      this.setLoadingFlage(false)
      this.$notify.error({
        title: "不明原因换个格式试一试",
        customClass: "alter"
      });
    },
    imgremove() {
      this.filelist.pop();
      //   保存图片路径
      this.navitemdata.img_url = null;
    },
    // 设置用户数据按钮
    setUserFn() {
      this.$refs["userdataForm"].validate(async valid => {
        if (valid) {
          // 打开加载
        this.setLoadingFlage(true)
          let { data: res } = await this.$http.put("/users", {
            new_password: this.loginUserData.password,
            username: this.loginUserData.username,
            head_src: this.loginUserData.head_img
          });
        this.setLoadingFlage(false)
          // 判断
          if (res.ok == 0)
            return this.$notify.error({
              title: res.error,
              customClass: "alter"
            });
          this.$notify.success({
            title: "修改成功~",
            customClass: "alter"
          });
          // 重新获取数据
          this.getLoginUserData()
        }
      });
    },
    // 上传开始
    headupload() {
      // 开启加载
      this.setLoadingFlage(true);
    },
    // 上传完成
    headsuccess(res) {
      // 关闭加载
      this.setLoadingFlage(false);
      // 判断
      if (res.ok == 0) {
        this.$notify.error({
          title: "图片不能大于4M哟~",
          customClass: "alter"
        });
        return;
      }
      this.loginUserData.head_img = res.src;
    }
  },
  created() {
    // 判断有没有token
    if (!this.isLogin) {
      return this.$router.push("/");
    }
    this.getLoginUserData();
    // 获取navs
    this.getnavs(() => {
      this.navs = this.showData.navs.split(",");
    });
  },
  // 监听
  watch: {
    showData: {
      handler(newVal) {
        this.navs = newVal.navs.split(",");
      },
      // 深度监听
      deep: true
    }
  }
};
</script>

<style>
.Usercontrol #conbox {
  display: flex;
  /* 自动换行 */
  flex-wrap: wrap;
}
.Usercontrol #conbox > div {
  flex: 1;
  width: 16%;
  min-width: 180px;
  max-width: 320px;
  height: 200px;
  margin: 0 auto;
  padding: 20px;
}
.Usercontrol #conbox > div > div {
  width: 100%;
  height: 100%;
  background-color: skyblue;
  text-align: center;
  font-size: 26px;
  line-height: 160px;
  background: linear-gradient(to right, #feac5e, #c779d0, #4bc0c8);
}

.Usercontrol #conbox > div > div.noselect {
  background: #f3f3f3;
  opacity: 0.8;
  color: gray;
  /* animation: gray 1s linear; */
  position: relative;
}
.Usercontrol #conbox > div > div.noselect::before {
  content: "关闭";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); /*2d移动*/
  width: 145%;
  height: 145%;
  background-color: black;
  opacity: 0.5;
  color: white;
  font-size: 30px;
  animation: zhezao 1s;
  border-radius: 50%;
  line-height: 232px;
}
.Usercontrol .outBtn {
  width: 100%;
  padding: 20px;
}
.Usercontrol .outBtn > div {
  width: 100%;
  text-align: center;
  background-color: red;
  height: 40px;
  line-height: 40px;
  margin: 0 auto;
  max-width: 500px;
}
@keyframes zhezao {
  0% {
    transform: translate(-50%, -50%) scale(0);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}
.Usercontrol .imgtitle {
  color: #feac5e;
  font-size: 12px;
  position: relative;
  top: 10px;
}
.Usercontrol .upnone .el-upload.el-upload--picture-card {
  display: none !important;
}
.Usercontrol .elForm div {
  overflow: visible;
}
</style>